html {
	width: 100%;
	height: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
	height: 100%;
}

a {
  text-decoration: none !important;
}

p {
  font-size: 0.8rem;
}
nav {
	margin: 0 7em 1rem;
}

a {
	padding-top: 2rem !important;
}

.nav-link {
	color: #fff !important;
}

li a.active {
	border-top: 3px solid #fff;
}

button {
	border-radius: 50px !important;
	margin-top: 2rem !important;
}
.main {
	margin-left: 7em;
	margin-right: 0;
	color: #fff;
}

h1 {
	font-weight: normal !important; 
}

.hero-image {
	padding-top: 5rem !important;
}
.Hero {
	background-image: url('/images/HeroBackground.png');
	background-size: cover;
	box-sizing: border-box;
}


.first-section {
	background-image: url('/images/curved.png');
	background-size: cover;
}

.contents {
	min-height: 450px;
	margin-right: 7rem;
}
.second-section {
	margin-right: 7rem;
}
.slideshow {
  background-image: url("/images/curved.png");
  background-size: cover;
}

.slick-slider {
  width: 90%;
  margin: 0 auto 3rem;
  padding-bottom: 3rem;
}

.slick-list {
  width: 90%;
  margin: 0 auto;
}

button {
  margin-top: 0 !important;
}

.slick-next,
.slick-prev {
  top: 35% !important;
}

.slick-next:before {
  color: #007bff;
  font-size: 30px !important;
}

.slick-prev:before {
  color: #007bff;
  font-size: 30px !important;
}

.slick-slide {
  padding: 1rem;
}

.slick-slide img {
  width: 100%;
  box-shadow: 0px 0px 17px 0px rgba(0, 0, 0, 0.75);
  border-radius: 0px;
}

.slick-slide img:hover {
  box-shadow: none;
}

.icon-holder {
	width: 5rem;
	height: 5rem;
	border-radius: 50%;
	box-shadow: 0px 0px 9px 0px rgba(0, 0, 0, 0.3);
}

.message {
	padding: 3rem 2rem;
	max-width: 30%;
	text-align: center;
	box-shadow: 0px 0px 17px 0px rgba(0,0,0,0.75);
}
